.App{
    height: auto;
    min-height: 100vh; 
    width: auto; 
    background-color: rgb(168, 173, 182);
    display: grid;
    place-items: center;
}

.app__container {
    min-height: 700px;  
    min-width: 500px;   
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(232, 234, 237);
    border-radius: 12px;
    padding: 32px 0;
}

.app__todoContainer{
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}